<template>
  <div :class="['zoo-home']">
    <div class="title"><img src="../assets/img/home/zoo-title.png"></div>

    <div class="abc-img"><img src="../assets/img/home/icon/icon_1.png"></div>

  </div>

</template>

<script>
  import { mapState } from "vuex"
//  import moment from 'moment'
//  import { addUser, updateUser, getUserList, getRandomWords } from '@/api/chooseWord'

  import getRandom from '../assets/js/getRandomArr'



  import '@/assets/js/m'

  export default {
    name: 'Home',
    data(){
      return {
        clientWidth: '', //
        clientHeight: '', //
        title: {}, // 


        animalCount_1: 10,
        animalCount_2: 20,
        animalCount_3: 24,

      }
    },
    computed: {
      ...mapState([
        'resourceUrl' // host
      ]),
    },
    created () {

      /*
        
        1.   5s10  5s10  5s20  5s20
             5s24  5s24  3s20  3s20
             3s24  3s24  2s24  1s24

             121 + 1 + 2 + 2 + 3 + 3 + 4 + 4 + 5 + 5 + 10 + 20

             
       */





      console.log(getRandom.getNorepeatArr(1, 360))

    },
    watch: {

    },
    mounted () {
      // 
      this.start()

      // doc
      doc.init(this)




    },
    methods: {
      // ()
      async start () {
        // 
        this.orientation()

        // zootitle
        this.getTitle()






      },


      // 
      orientation () {
        var that = this

        //  CSS3   90 
        let detectOrient = function() {
          let width = document.documentElement.clientWidth,
            height =  document.documentElement.clientHeight

          if( width >= height ){ // 

            let style = {
              width: width + 'px',
              height: height + 'px',
              '-webkit-transform': 'rotate(0)',
              'transform': 'rotate(0)',
              '-webkit-transform-origin': '0 0',
              'transform-origin': '0 0'
            }

            $('.zoo-home').css(style)

            // 
            that.clientWidth = width
            that.clientHeight = height

          }
          else{ // 
            let style = {
              width: height + 'px',
              height: width + 'px',
              '-webkit-transform': 'rotate(90deg)',
              'transform': 'rotate(90deg)',
              '-webkit-transform-origin': width / 2 + 'px ' + width / 2 + 'px',
              'transform-origin': width / 2 + 'px ' + width / 2 + 'px'
            }

            $('.zoo-home').css(style)

            // 
            that.clientWidth = height
            that.clientHeight = width

          }

        }
        window.onresize = detectOrient;
        detectOrient();
      },

      // zootitle
      getTitle () {
        const titleWidth = $('.title').outerWidth()
        const titleHeight = $('.title').outerHeight()

        this.title = {
          position: [0, 0],
          width: titleWidth,
          height: titleHeight
        }

      }
    },
    components: {

    }

  }

  var doc = {
    vueObj: '', // vue

    init (that) {
      this.vueObj = that

      this.helper()
      this.bind()
    },
    helper () {

    },
    bind () {

    },

  }

</script>

<style lang="scss">
  @import "../../../assets/css/pr2rem";
  @import "../assets/css/index";

  .zoo-home {
    position: relative;
    width: 100%;
    height: 100%;
    background: url("../assets/img/home/bg.jpg") no-repeat center / cover;

    .title {
      width: pr(300);
      height: pr(160);
    }

    & > .abc-img {
      position: absolute;
      width: pr(80);
      height: pr(80);
    }

  }

</style>





